	export default {
		data() {
			return {
			news:[],
			code:''
			}
		},
		onLoad() {
uni.request({
	url: 'https://unidemo.dcloud.net.cn/api/news',
	method: 'GET',
	data: {},
	success: res => {
		//console.log(res.data);
		this.news=res.data;
		this.code=res.data;
		
	},
	fail: () => {},
	complete: () => {}
});
		},
		methods: {
	openinfo(e){
		//console.log(e);
var newid=e.currentTarget.dataset.newid
//console.log(newid);
		uni.navigateTo({
			url: '../info/info?newid='+newid,
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
}
		},
		 filters: {
			 qtime: function (value) {
				 
				 return  value.split(" ")[0]
			 } ,
			 qstr: function (str) {
			 				 
							 if(str.length > 30){
								str = str.substring(0,30) +"..."; 
							 } 
			 				 return  str
			 } 
		 }
	}
